  .links__list {
    list-style-type: none; }
  .links__item {
    line-height: 2.5em;
    text-align: center; }
    .links__item img {
      height: 15px; }

main {
  padding: 40px;
  font-weight: 700;
  position: relative; }

.sideListContainer {
  position: absolute;
  width: 250px;
  right: -25%;
  top: 80%;
  text-align: left; }
  .sideListContainer__list {
