      padding-left: 44px;
      margin-bottom: 10px; }
    .sideListContainer__list li:first-of-type {
      background-image: url(../assets/Triangle.svg);
      background-repeat: no-repeat;
      background-position: 0% 10%; }

.main-photo__image {
  width: 100%; }

.recommendation {
  font-size: 24px;
  font-style: oblique;
  float: right;
  width: 30%;
  height: 50%;
  margin: 60px -80px 0 30px;
  text-align: left; }
  .recommendation__heading {
    font-size: 48px;
    position: relative;
    margin-bottom: 80px; }
    .recommendation__heading::after {
      content: "";
      position: absolute;
      height: 5px;
      border-bottom: 4px solid #5590AC;
      bottom: -30%;
      width: 40%;
      left: 0%; }

.main-photo {
  margin: 50px 0; }

.footer {
  text-align: center;
  padding: 10px 0 60px 0;
  background: url(../assets/booking_bg.png) no-repeat;
  background-size: cover;
  height: 100%; }

.header__footer {
  color: #fff;
  margin-bottom: 60px;
  text-align: left;
  font-size: 64px;
  font-weight: bold; }
  .header__footer span {
    display: inline-block;
    position: relative; }
    .header__footer span::after {
      content: "";
      position: absolute;
      height: 5px;
      border-top: 4px solid #fff;
      top: 50%;
      width: 100%;
      left: 100%;
      margin-left: 25px; }

.hasDatepicker,
.datepicker {
  background: rgba(44, 44, 44, 0.6) !important;
  flex: 1;
  margin: 0 !important;
  padding: 20px 30px !important; }

.ui-datepicker-header,
.ui-datepicker-calendar a {
  color: #fff !important; }

.form__submit {
  display: flex;
  justify-content: space-between; }

.form__submit > * {
  border-radius: 16px; }

.form__details {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 20px;
  padding: 50px;
  text-align: center;
  flex: 1;
  width: 400px; }

.form__details > * {
  display: block;
  margin-bottom: 20px;
  border-radius: 50px;
  padding: 10px;
  width: 100%;
  background-color: #EBF2FA;
  border: none;
  padding: 20px;
  font-size: 18px;
  font-weight: bold; }

input[type="submit"] {
  color: #fff;
  background-color: #348EB3;
  margin: 30px 0 10px 0;
  box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.2);
  transition: all .4s; }
  input[type="submit"]:hover {
    cursor: pointer;
    transform: scale(1.05); }

select {
  -moz-appearance: none;
  /* Firefox */
  -webkit-appearance: none;
  /* Safari and Chrome */
  appearance: none;
  background: url(../assets/arrows.svg) 95% 44% no-repeat; }
  select:hover {
    cursor: pointer; }

*, *:before, *:after {
  box-sizing: border-box; }

html {
  font-size: 18px; }

body {
  font-family: "Roboto", sans-serif;
  font-size: 1em;
  line-height: 1.6; }

.datepicker {
  width: 400px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.2);
  margin: 50px auto;
  overflow: hidden; }
  .datepicker .ui-datepicker-inline {
    padding: 30px; }
  .datepicker .ui-datepicker-header {
    text-align: center;
    padding-bottom: 1em;
    text-transform: uppercase;
    letter-spacing: .1em; }
    .datepicker .ui-datepicker-header .ui-datepicker-prev,
    .datepicker .ui-datepicker-header .ui-datepicker-next {
      display: inline;
      float: left;
      cursor: pointer;
      font-size: 1.4em;
      padding: 0 10px;
      margin-top: -10px;
      color: #CCC; }
    .datepicker .ui-datepicker-header .ui-datepicker-next {
      float: right; }
  .datepicker .ui-datepicker-calendar {
    width: 100%;
    text-align: center; }
    .datepicker .ui-datepicker-calendar thead {
      color: #CCC; }
    .datepicker .ui-datepicker-calendar tr th, .datepicker .ui-datepicker-calendar tr td {
      padding-bottom: .5em; }
    .datepicker .ui-datepicker-calendar a {
      color: #444;
      text-decoration: none;
      display: block;
      margin: 0 auto;
      width: 35px;
      height: 35px;
      line-height: 35px;
      border-radius: 50%;
      border: 1px solid transparent;
      cursor: pointer; }
    .datepicker .ui-datepicker-calendar .ui-state-highlight {
      border-color: #5590AC;
      background-color: #5590AC;
      color: #5590AC; }

.highlightdays a {
  color: #fff !important; }

.ui-datepicker-header, .ui-datepicker-calendar a {
  color: #fff !important; }

.ui-state-active {
  background: #5590AC; }

.ui-datepicker-today a.ui-state-highlight {
  border-color: #d3d3d3;
  color: #555555; }

.ui-state-highlight {
  background-color: transparent !important; }

tr .ui-state-disabled {
  color: #999;
  background-image: url(../assets/circle.png);
  background-repeat: no-repeat;
  background-size: 6px 6px;
  background-position: 80% 0; }

.ui-datepicker-other-month {
  background: transparent !important; }

@media only screen and (max-width: 1400px) {
  .sideListContainer {
    position: relative;
    left: 0;
    margin-top: 20px;
    border-top: 1px solid #000;
    padding-top: 20px;
    padding-left: 40px;
    width: 100%; }
    .sideListContainer__list li {
      list-style-position: inside;
      padding-left: 0px;
      margin-bottom: 10px; }
    .sideListContainer__list li:first-of-type {
      position: relative;
      background: none; }
      .sideListContainer__list li:first-of-type:before {
        content: "";
        position: absolute;
        left: -60px;
        top: 10%;
        width: 50px;
        height: 50px;
        background-image: url(../assets/Triangle.svg);
        background-repeat: no-repeat;
        background-position: 0% 10%;
        -webkit-transform: rotate(90deg);
        -moz-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        -o-transform: rotate(90deg);
        transform: rotate(90deg); } }

